import config from "../../../../config";
import style from './index.module.css'
import eventsList from '../../modules/events/eventList'
import {NavLink} from "react-router-dom";

const Index = (props) => {
    return (
        <main>
            <div className={style.block}>
                <h1>Ивенты</h1>

                {/*<NavLink to={`${config.repo}/puzzle`}>Puzzle</NavLink>*/}

                <div className={style.carousel}>
                    {eventsList.map(eventName =>
                        <NavLink to={eventName.href ? `${config.repo}${eventName.href}` : `${config.repo}/`}
                                 key={eventName.name}>
                            <div className={style.element}
                                 style={{position: 'relative', ...(eventName.style ? eventName.style : {})}}>
                                <h2 style={{
                                    color: eventName.style.color && eventName.style.color,
                                    filter: 'drop-shadow(0 .1rem .5rem #333)',
                                    textAlign: 'center'
                                }}>{eventName.name}</h2>
                                <p style={{
                                    color: eventName.style.color && eventName.style.color,
                                    filter: 'drop-shadow(0 .1rem .5rem #333)'
                                }}>{eventName.desc}</p>
                                {eventName &&
                                <div style={{
                                    position: "absolute",
                                    top: 0,
                                    left: 0,
                                    height: '100%',
                                    width: '100%',
                                    background: 'rgba(0,0,0,.3)',
                                    borderRadius: style.element.borderRadius && style.element.borderRadius,
                                    zIndex: -1
                                }}/>
                                }
                            </div>
                        </NavLink>
                    )}
                </div>
            </div>
        </main>
    )
}

export default Index